@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
.navbar {
  background-color: #3498db !important;
}





.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active {

  font-weight: bold;
  border-bottom: 1px solid black;

}

.navbar a:hover {
  color: aqua !important;
}

@media (max-width: 991px) {
  .navbar-brand {
    font-size: 16px !important;
  }

  .menu_active {
    font-weight: bold;
    border-bottom: none;
  }

}


/*=============================================*/


#header {
  width:100% ;
  height: 80vh;
  margin-top:70px;
}

#header h1 {
  margin : 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}


#header .brand-name {
  color: #3498db;
}

#header h2 {
  color: #484848;
  font-size: 24px;;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: 2px solid #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
a{
  color: #3498db ;
}

#header .btn-get-started:hover 
{
  background: #3498db;
  color: white

}

.header-img {
  text-align: right ;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}


@keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

}

